.container{
    width: 300px;
    height: 450px;
    background-color: rgb(139, 139, 139);
    border-radius: 10px;
    margin: 20px;
}
.imgs{
    width: 200px;

}
h1{
    margin: 0;
    padding: 0;
}
button{
    width: 190px;
    height: 50px;
    font-size: 1.1em;
    margin: 20px;
    cursor: pointer;
}
button:active{
    transform: scale(0.9);
}
.slickText{
    font-family: 'Dancing Script', cursive;
    font-weight: bolder;
    color: rgb(230, 130, 130);
}
